.to-top {
	cursor: pointer;
	top: 5px;;
	content: "";
	display: block;
	width: 30px;
	height: 30px;
	position: absolute;
	right: -200px;
	opacity: 0.5;
	@include background(transparent, 'to-top.png', 50%, 50%);
	&:hover {
		opacity: 0.7;
	}
	&:active {
		opacity: 1;
	}
}
.to-top-pos {
	width: 770px;
}
.main__to-top-pos {
	margin: 0 auto;
	position: relative;
}