.list-updates {
	border-collapse: collapse;
}
.main__column-list-updates_size-min {
	min-width: 175px;
}
.main__column-list-updates_size-normal {
	min-width: 230px;
}
.main__column-list-updates_size-big {
	min-width: 315px;
}
.column-list-updates {
	font-family: $font-stack;
	color: $table-nav-color;
	font-size: 14px;
	line-height: 24px;
	font-weight: 600;
}
.main__column-list-updates_size {
	font-size: 16px;
	color: $content-table-color;
}
.main__column-list-updates_color {
	color: $default-color;
}
.main__column-list-updates_bg {
	padding-left: 25px;
	@include background(transparent, 'calendar-icon.png', 0%, 4px);
}
.paragraph-list-updates {
	cursor: pointer;
	&:hover .main__column-list-updates_hover {
		background-color: $content-color;
	}
	&:hover .main__column-list-updates_hover-blue {
			color: $active-color;
	}
	&:hover .main__column-list-updates_hover-gray {		
		color:$content-table-color
	}	
}
.main__column-list-updates_padding {
	padding-left: 30px;
}
.main__project-history_margin {
	margin-bottom: 30px;
}
.main_padding {
	padding-bottom: 1px;
}