@mixin border-bottom($size: 1px, $style: solid, $color: white){
	border-bottom: $color $size $style;
}
@mixin border-top($size: 1px, $style: solid, $color: white){
	border-top: $color $size $style;
}
@mixin border($size: 1px, $style: solid, $color: white){
	border: $color $size $style;
}
@mixin background($color, $img, $x: 0, $y: 0){
	background: $color url('../img/#{$img}') no-repeat $x $y;
}