.header {
	width: 100%;
	background-color: $header-color;
	@include border-bottom(1px, solid, $active-color);
}
.container {
	width: 1240px;
}
.header__container {
	margin: 0 auto;
}
.logo {
	width: 120px;
	height: 31px;
	@include background(transparent, 'logo.png', 50%, 50%)
}
.header__logo {
	margin-top: 15px;
	float: left;
}
.search {
	width: 300px;
	height: 36px;
}
.header__search {
	float: left;
	margin-top: 15px;
	margin-left: 50px;
}
.entry-field {
	outline: none;
	@include border(1px, $border-color, solid);
	height: 35px;
	line-height: 35px;
	font-family: $font-stack;
	font-size: 14px;
	width: 100%;
	border-radius: 4px;
	@include background($content-color , 'search.png', 285px, 50%)
	&:hover{
		border-color: $active-color;
	}
	&:focus{
		border-color: $input-active-color;
		background-color: #fff;
	}		
}
.header__entry-field {
	padding-left: 15px;
}
.nav {
	list-style-type: none;
}
.header__nav {
	margin: 0;
	margin-left: 45px;
	float: left;
}
.header__nav-paragraph {
	float: left;
	padding: 0 20px;
}
.paragraph-link {
	display: inline-block;
	text-decoration: none;
	font-family: $font-stack;
	color: $header-link-color;
	font-size: 16px;
}
.header__paragraph-link	{
	line-height: 65px;
	&:hover {
		color: $active-color;
	}
	&:active {
		@include border-bottom(5px, solid, $active-color);
	}
}
.social-groups {
	list-style-type: none;
	padding: 0;
}
.header__social-groups {
	float: right;
	margin: 0;
	margin-top: 23px;
	margin-left: 12.5px;
}
.header__paragraph {
	float: left;
	margin: 0 20px;
}
.social-link {
	display: inline-block;
	width: 20px;
	height: 18px;
	opacity: 0.5;
	line-height: 65px;
	&:hover{
		opacity: 0.7;
	}
	&:active{
		opacity: 1;
	}
}
.header__social-link_telegram {
	@include background(transparent, 'telegram-icon.png', 50%, 50%);
}
.header__social-link_twitter {
	@include background(transparent, 'twitter-icon.png', 50%, 50%);	
}
.header__social-link_sheets {
	@include background(transparent, 'sheets-icon.png', 50%, 50%);
}
.header__paragraph_indentation {
	margin-right: 0;
}
