.load-more {
	font-family: $font-stack;
	font-size: 14px;
	color: $header-color;
	display: block;
	width: 770px;
	line-height: 35px;
	border-radius: 4px;
	background-color: $button-color;
	text-decoration: none;
	&:hover {
		background-color: #8494a7;
	}
	&:active {
		background-color: $content-table-color;
	}
}
.main__load-more {
	position: relative;
	margin: 0 auto;	
	text-align: center;
	position: relative;
}
