.footer {
	@include border-top(1px, solid, $active-color);
}
.footer__container {
	margin: 0 auto;
	padding-bottom: 70px;
}
.content-disclamer {
	width: 320px;
}
.footer__content-disclamer {
	float: left;
}
.footer__logo {
	margin-top: 15px;
	float: left;
}
.reviewers {
	padding-top: 23.5px;
	font-family: $font-stack;
	font-size: 16px;
	color: $table-nav-color;
	text-decoration: none;
	display: inline-block;
}
.footer__reviewers {
	float: left;
	margin-left: 14px;
}
.disclamer {
	font-family: $font-stack;
	font-weight: 400;
	font-style: italic;
	color: $table-nav-color;
	display: inline-block;
	font-size: 11px;
	line-height: 16px;
	margin: 0;
	margin-top: 71px;
}
.attention {
	color: $attention-color;
	font-weight: 700;
}
.footer__attention {
	position: relative;
}
.attention::before {
    content: "*";
    display: block;
    left: -15px;
    top: 3px;
    font-size: 17px;
    position: absolute;
}
.feedback {
	list-style-type: none;
	margin: 0;
}
.footer__icos {
	float: right;
}
.footer__link-paragraph {
	&:hover {
		color: $active-color;
	}
}
.footer__link-paragraph_hover:hover {
	color: $table-nav-color;
}
.footer__link-paragraph_size {
	font-size: 14px;
	line-height: 25px;
}
.footer__link-paragraph_uppercase {
	text-transform: uppercase;
}
.footer__link-paragraph_title {
	color: $table-nav-color;
	padding: 15px 0; 
	width: 170px;
	display: block;
	margin-bottom: 4px;
	@include border-bottom(1px, solid, $border-color);
}
.social-groups {
	list-style-type: none;
	padding: 0;
}
.footer__social-groups {
	float: right;
	margin: 0;
	margin-top: 18px;
	margin-left: 12.5px;
}
.footer__paragraph {
	float: left;
	margin: 0 12px;
}
.social-link {
	display: inline-block;
	width: 20px;
	height: 18px;
	opacity: 0.5;
	line-height: 65px;
	&:hover{
		opacity: 0.7;
	}
	&:active{
		opacity: 1;
	}
}
.footer__social-link_telegram {
	@include background(transparent, 'telegram-icon.png', 50%, 50%);
}
.footer__social-link_twitter {
	@include background(transparent, 'twitter-icon.png', 50%, 50%);	
}
.footer__social-link_mail {
	@include background(transparent, 'mail-icon.png', 50%, 50%);
}
.footer__link-paragraph_width {
	width: 275.5px;
}
.footer__paragraph_indentation {
	margin-right: 0;
}
.footer__link-paragraph_copywriting::after {
	content:"Â© 2018 Top 7 ICO";
	font-size: 11px;
	color: $table-nav-color;
	font-family: $font-stack;
	float: right;
}