.main__service-list-characteristic {
	opacity: 0;
}

.main__spacer-bg {
	position: relative;
}
.spacer-bgpos {
	height: 54px;
	width: 973px;
	background-color: $content-color;
}
.main__spacer-bg {
	z-index: 9;
	position: relative;
}
.main__spacer-bgpos {
	z-index: 9;
	position: absolute;
	top: -8px;
	left: -26px;
}
.main__spacer-bgpos_width {
	height: 88px;
    top: -41px;
}
.main__service-list-characteristic_spacer {
	z-index: 9;
	position: fixed;
	margin-bottom: 40px;
	top: -3px;;
	left: 50%;
	transform: translateX(-50%);
	display: none;
}
.main__service-list-characteristic_show {
	display: table-row;
	opacity: 1;
	z-index: 10;
}
.main__service-list-characteristic_show-scroll {
	position: fixed;
	top: 20px;
	left: 50%;
	transform: translateX(-50%);
	min-width: 928px;
}
.main__service-list-characteristic_hidden {
	opacity: 0;
}
.main__service-list-characteristic_pos {
	position: absolute;
	top: 31px;
	left: 50%;
	transform: translateX(-50%);
	min-width: 928px;
}
.service-characteristic-header {
	z-index: 10;
	position: relative;
	font-size: 11px;
	color: $table-nav-color;
	font-family: $font-stack;
	text-transform: uppercase;
	cursor: pointer;
	line-height: 32px;
	min-width: 125px;

}
.main__service-characteristic-header_width-main {
	min-width: 320px;
}
.main__service-characteristic-header_width-min {
	min-width: 80px;
}
.server-hover {
	content: "";
	display: inline-block;
	width: 10px;
	height: 8px;
	padding-left: 10px;
	@include background(transparent, 'ic-sort.png', 50%, 50%);
	&:hover {
		@include background(transparent, 'ic-sort-hover.png', 50%, 50%);
	}
	&:active {
		@include background(transparent, 'ic-sort-pressed.png', 50%, 50%);		
	}
}
.service-info {
	width: 970px;
	height: 120px;
}
.service-info::after {
	content: "";
	display: block;
	margin-top: 10px;
}
.main__service_position {
	position: relative;
}
.service-characteristic {

	font-family: $font-stack;
	font-size: 14px;
	color: $content-table-color;
}
.main__service-info_width {
	min-width: 284px;
}
.main__service-characteristic {
	position: relative;
}
.main__service-info {
	position: relative;
}
.main__bgpos-service-info {
	position: relative;
}
.bg-service-info  {
	border-radius: 3px;
	width: 970px;
	height: 120px;	
	background-color: #fff;
	@include border(1px, solid, $border-color);
}
.main__bg-service-info {
	position: absolute;
	top: 0;
	left: -26px;
}
.main__bg-service-info-active {
	background-color: $top-service-color;
}
.service-icon-container {
	margin-right: 24px;
}
.service-icon {
	width: 24px;
	height: 24px;
	display: block;
	background-position: 50% 50%;
}
.main__service-icon {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
}
.service-icon-container  {
	width: 52px;
	height: 52px;
	border-radius: 50%;
	box-shadow: 0px 1px 2px 2px $border-color;
	background-color: $header-color;	
}
.main__service-icon-container {
	position: relative;
	float: left;
}
.service-name {
	font-family: $font-stack;
	font-size: 18px;
	color: $content-table-color;
	text-decoration: none;
	transition: 0.3s;
	&:hover {
		color: $active-color;
	}
	&:active {
		color: #70577b;
	}
}
.service-type {
	display: inline-block;
	font-family: $font-stack;
	font-size: 14px;
	color: $table-nav-color;
}
.main__service-type {
	margin-top: 10px;
}
.main__service-role {
	padding: 4px 10px;
	margin-top: 16px;	
}
.service-role {
	font-family: $font-stack;
	font-size: 13px;
	color: $header-color;
	display: inline-block;

	border-radius: 15px;;
}
.main__service-role_sponsored {
	background-color:  #92ad92;
}
.main__service-role_high {
	background-color: #c08bdf;
}
.main__service-role_medium {
	background-color: #7fc3e0;
}
.paragraph-link:hover {
	color: $active-color;
}
.service-characteristic-link {
	color: $header-link-color;
	text-decoration: none;
}
.service-characteristic-link:hover {
	color: $active-color;
}
.main__service-characteristic-link_notactive {
	color: $not-active-color;
}
.main__service-characteristic-header_width-reviewers {
	min-width: 280px;
	box-sizing: border-box;
}
.people-counter {
	@include background(transparent, 'people-counter-icon.png', 0%, 0%);
	display: inline-block;
	font-family: $font-stack;
	font-size: 14px;
	color: $content-table-color;
	min-width:20px;
	min-height: 20px;	
}
.main__people-counter {
	margin-left: 35px;
	padding-left: 20px;
}
.main__server-hover_posleft {
	padding-bottom: 2px;
}
.main__people-counter_posleft {
	margin-left: 61px;
	padding: 0;
	padding-top: 13px;
	min-height: 20px;
	background-position: 0 100%;
}
.main__service-role_percent {
	background-color: $percent-color;
}
.service-characteristic-full  {
	font-family: $font-stack;
	font-size: 11px;
	color: $content-table-color;
	font-weight: 600;
	min-width: 100px;
	max-width: 100px;
}
.main__service-characteristic-full {
	padding-top: 11px;
	text-align: center;
}	
.service-characteristic-scroll {
	max-width: 611px;
	overflow-x: hidden;
	z-index: 9;
	position: relative;
}

.server-info-scroll {
	display: inline-block;
	width: 22px;
	height: 22px;
	background-color: $header-color;
	@include border(1px, solid, $border-color);
	@include background($header-color, 'trangle-disabled-icon.png', 40%, 50%);	
	border-radius: 3px;
	opacity: 0.5;
	&:hover {
		opacity: 0.7;
	}
	&:active {
		opacity: 1;
	}
}
.main__server-info-scroll {
	position: absolute;
	top: 5px;
	right: 5px;
}
.main__server-info-scroll_right {
	transform: rotate(180deg);
	position: absolute;
	bottom: 10px;
	right: -24px;
}
.main__server-info-scroll_active {
	opacity: 0.8;
	@include background($header-color, 'trangle-icon.png', 40%, 50%);	
	&:hover {
		opacity: 1;
		border-color: $table-nav-color;
	}
	&:active {
		border-color: $active-color;
	}
}
.server-info-scroll-block {
	cursor: pointer;
	position: relative;
	min-width: 25px;
	z-index: 10;
}
.server-info-scroll-num {
	font-family: $font-stack;
	font-size: 11px;
	color: $content-table-color;
	font-weight: 600;
	width: 10px;
	height: 10px;
	border: none;
	outline: none;
	background-color: transparent;
	opacity: 0.7;
}
.main__server-info-scroll-num {
	position: absolute;
	top: -18px;
    right: 0;
}
.main__server-info-scroll-num_pos {
	top: -18px;
	right: -10px;
}
.main__server-info-scroll-num_active {
	font-size: 14px;
	opacity: 1;
	left: 594px;
}
.main__server-info-scroll-num_show {
	opacity: 0;
}
.main__list-service-scroll_pos {
	top: -5px;
}
.main__server-hover_pos-top {	
	position: relative;
    top: 8px;
    right: -28px;
} 