.full-list-social {
	list-style-type: none;
}
.body__full-list-social {
	margin: 0;
	padding: 0;
}
.body__paragraph-list {
	float: left;
}
.full-social-link {
	display: block;
	width: 20px;
	height: 20px;
}
.body__full-social-link_telegram {
	@include background(transparent, 'telegram-min-icon.png', 50%, 50%);
}
.body__full-social-link_twitter {
	@include background(transparent, 'twitter-min-icon.png', 50%, 50%);
}
.body__full-social-link_github {
	@include background(transparent, 'github-icon.png', 50%, 50%);
}
.body__full-social-link_facebook {
	@include background(transparent, 'facebook-icon.png', 50%, 50%);
}
.body__full-social-link_medium {
	@include background(transparent, 'medium-icon.png', 50%, 50%);
}
.body__full-social-link_youtube {
	@include background(transparent, 'youtube-icon.png', 50%, 50%);
}