.detailed-info-project {
	width: 835px;
}
.main__detailed-info-project {
	margin: 0 auto;
	padding-top: 65px;
	margin-bottom: 80px;
}
.project-history {
	box-sizing: border-box;
	width: 835px;
	background-color: $header-color;
	@include border(1px, solid, $border-color);
	border-radius: 4px;
}
.main__project-history {
	padding: 20px 25px; 
	margin-bottom: 10px;
}
.main__service-characteristic_float {
	float: left;
	width: 380px;
}
.main__text-project-history {
	position: relative;
	width: 400px;
	float: left;

}
.text-project-history::after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 1px;
	height: 75px;
	background-color: $border-color;
}
.paragraph-project-history {
	font-family: $font-stack;
	font-size: 13px;
	line-height: 19px;
	color: $content-table-color;
}
.main__paragraph-project-history {
	margin: 0;
	padding-left: 30px;
}
.main__service-characteristic-link_icon-website {
	cursor: pointer;
	display: inline-block;
	padding-top: 18px;
	padding-left: 23px;
	padding-right: 23px;
	@include background(transparent, 'website-icon.png', 0%, 102%);
}
.main__service-characteristic-link_icon-papper {
	cursor: pointer;
	display: inline-block;
	padding-top: 18px;
	padding-left: 23px;
	@include background(transparent, 'papper-icon.png', 0%, 102%);
}
.list-project-history {
	border-collapse: collapse;
}
.column-project-history {
	font-family: $font-stack;
	font-size: 14px;
	color: $content-table-color;
	font-weight: 600;
	
}
.main__column-project-history {
	padding: 4px 0;
}
.main__column-project-history_border {
	padding-bottom: 11px;
	@include border-bottom(1px, solid, $border-color);
}
.main__column-project-history_padding {
	padding-top: 11px;
}
.main__project-history_column-left {
	float: left;
	max-width: 400px;
	padding: 10px 25px;
}
.main__project-history_column-right {
	min-width: 82px;
}
.main__column-project-history_uper {
	text-transform: uppercase;
}
.main__column-project-history_left {
	min-width: 260px;
}
.main__service-role_margin {
	margin-top: 0;
	font-weight: 400;
	margin-bottom: 10px;
}
.main__column-project-history_color {
	color: $table-nav-color;
}
.main__column-project-history_size {
	font-size: 13px;
}
.main__column-project-history_color-darker {
	color: $content-table-color
}
.main__project-history_column_pos-right {
	float: right;
	max-width: 425px;
	padding: 11px 25px; 
}
.main__column-project-history_left-size {
	min-width: 200px;
}
.main__column-project-history_right-size {
	min-width: 175px;
}
